.rcfm-form-condition-editor-branch-title
  margin: $form-condition-editor-branch-title-margin
  font-size: $form-condition-editor-branch-title-font-size
  font-weight: $form-condition-editor-branch-title-font-weight
  overflow: hidden
  line-height: 1
  white-space: nowrap
  text-overflow: ellipsis

.rcfm-form-condition-editor-when-branch

  & > .rcfm-btngroup-x
    margin: $form-condition-when-branch-button-group-margin

.rcfm-form-condition-editor-when-branch-rule
  margin: $form-condition-editor-when-branch-rule-margin

  &:first-child
    margin-top: 0

  &:last-child
    margin-bottom: 0

.rcfm-form-condition-editor-when-branch-rule-elements
  display: flex
  justify-content: space-between
  align-items: center
  margin: $form-condition-editor-when-branch-rule-elements-margin

  &:first-child
    margin-top: 0

  &:last-child
    margin-bottom: 0

  & > [class^=rcfm-]
    flex: 1 1 auto
    margin: $form-condition-editor-when-branch-rule-element-margin

  & > .rcfm-form-condition-editor-when-branch-rule-logic
    flex: 0 0 $form-condition-editor-when-branch-rule-logic-width

  & > .rcfm-btngroup-x
    flex: 0 0 auto

  & > [class^=rcfm-]:first-child
    margin-left: 0

  & > [class^=rcfm-]:last-child
    margin-right: 0
