.rcfm-fmtag::-webkit-input-placeholder
  color: $form-helper-placeholder-color

.rcfm-fmtag::-moz-placeholder
  opacity: 1
  color: $form-helper-placeholder-color

.rcfm-fmtag:-ms-input-placeholder
  color: $form-helper-placeholder-color

.rcfm-fm-invalid .rcfm-fmtag:not(:focus)::-webkit-input-placeholder
  color: $form-helper-placeholder-invalid-color

.rcfm-fm-invalid .rcfm-fmtag:not(:focus)::-moz-placeholder
  color: $form-helper-placeholder-invalid-color

.rcfm-fm-invalid .rcfm-fmtag:not(:focus):-ms-input-placeholder
  color: $form-helper-placeholder-invalid-color

.rcfm-fmtag:focus::-webkit-input-placeholder,
.rcfm-fmtag:focus ~ .rcfm-fmtag::-webkit-input-placeholder
  color: $form-helper-placeholder-focus-color

.rcfm-fmtag:focus::-moz-placeholder,
.rcfm-fmtag:focus ~ .rcfm-fmtag::-moz-placeholder
  color: $form-helper-placeholder-focus-color

.rcfm-fmtag:focus::-ms-input-placeholder,
.rcfm-fmtag:focus ~ .rcfm-fmtag::-ms-input-placeholder
  color: $form-helper-placeholder-focus-color



.rcfm-fmhint,
.rcfm-fmerror
  display: block
  width: 100%
  font-size: $form-helper-font-size
  line-height: $form-helper-line-height

.rcfm-fmhint
  color: $form-helper-hint-color

.rcfm-fmerror
  color: $form-helper-error-color

.rcfm-fmhint-small.rcfm-fmhint,
.rcfm-fmerror-small.rcfm-fmerror
  font-size: $form-helper-small-font-size
  line-height: $form-helper-small-line-height

.rcfm-fmhint-large.rcfm-fmhint,
.rcfm-fmerror-large.rcfm-fmerror
  font-size: $form-helper-large-font-size
  line-height: $form-helper-large-line-height
