[class^=rcfm-btn-]:-moz-focusring
  outline: none

[class^=rcfm-btn-]::-moz-focus-inner
  padding: 0
  border-style: none

[class^=rcfm-btn-]
  -webkit-appearance: none
  -moz-appearance: none
  appearance: none
  display: inline-flex
  vertical-align: top
  flex: 0 0 auto
  align-items: center
  justify-content: center
  max-width: 100%
  min-width: $button-height
  height: $button-height
  padding: $button-padding-em
  font-size: $button-font-size
  font-family: inherit
  line-height: 1
  word-break: normal
  white-space: nowrap
  overflow: hidden
  border: $button-border-style $button-border-width transparent
  border-radius: $button-border-radius
  cursor: pointer
  text-decoration: none
  text-transform: none
  background: none
  -ms-touch-action: manipulation
  touch-action: manipulation
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none

[class^=rcfm-btn-][disabled]
  cursor: not-allowed
  pointer-events: auto
  opacity: $button-disabled-opacity

[class^=rcfm-btn-] > *
  flex: 0 0 auto

[class^=rcfm-btn-].rcfm-btn-small
  min-width: $button-small-height
  height: $button-small-height
  font-size: $button-small-font-size

[class^=rcfm-btn-].rcfm-btn-large
  min-width: $button-large-height
  height: $button-large-height
  font-size: $button-large-font-size

[class^=rcfm-btn-].rcfm-btn-fluid
  display: flex
  flex: 1 1 auto
  width: 100%

[class^=rcfm-btn-].rcfm-btn-circle
  border-radius: 9999px
