[class^=rcfm],
[class^=rcfm]::before,
[class^=rcfm]::after
  box-sizing: border-box
  padding: 0
  margin: 0
  background-repeat: no-repeat

[class^=rcfm]::before,
[class^=rcfm]::after
  vertical-align: inherit
  text-decoration: inherit

[class^=rcfm]
  font-size: $base-font-size
  line-height: $base-line-height
  -ms-overflow-style: $base-scrollbar-edge-overflow-style
  -ms-touch-action: manipulation
  touch-action: manipulation
  -ms-text-size-adjust: 100%
  -webkit-text-size-adjust: 100%
  // sass-lint:disable no-misspelled-properties
  scrollbar-width: $base-scrollbar-firefox-width-keyword
  scrollbar-color: $base-scrollbar-firefox-color
  // sass-lint:enable no-misspelled-properties

// fix ios safari not fire mouse event on some elements
+browser-ios-safari
  .rcfm
    cursor: pointer

+browser-chrome-and-opera

  [class^=rcfm]::-webkit-scrollbar
    width: $base-scrollbar-vertical-width
    height: $base-scrollbar-horizontal-height

  [class^=rcfm]::-webkit-scrollbar-thumb
    background: $base-scrollbar-thumb-background
    border-radius: $base-scrollbar-thumb-border-radius

  [class^=rcfm]::-webkit-scrollbar-corner
    display: none

  [class^=rcfm]::-webkit-resizer
    display: none
