input.rcfm-fmtag,
select.rcfm-fmtag,
textarea.rcfm-fmtag
  -webkit-appearance: none
  -moz-appearance: none
  appearance: none
  display: block
  font-size: inherit
  line-height: inherit
  color: inherit
  font-family: inherit
  border-radius: 0

label.rcfm-fmtag,
optgroup.rcfm-fmtag,
option.rcfm-fmtag
  font-size: inherit
  color: inherit
  font-family: inherit

// prevent word break in safari
input.rcfm-fmtag,
select.rcfm-fmtag,
textarea.rcfm-fmtag,
optgroup.rcfm-fmtag,
option.rcfm-fmtag
  word-break: normal

// Remove the inner border and padding in Firefox.
.rcfm-fmtag::-moz-focus-inner
  padding: 0
  border-style: none

// Show the overflow in Edge and IE.
input.rcfm-fmtag
  overflow: visible

// Remove the inheritance of text transform in Firefox.
select.rcfm-fmtag
  text-transform: none

// 1. Remove the default vertical scrollbar in IE.
// 2. Change the resize direction on textareas in all browsers (opinionated).
textarea.rcfm-fmtag
  overflow: auto // 1
  -webkit-overflow-scrolling: touch
  resize: vertical // 2

// Correct the cursor style of increment and decrement buttons in Chrome.
.rcfm-fmtag::-webkit-inner-spin-button,
.rcfm-fmtag::-webkit-outer-spin-button
  height: auto

// 1. Correct the odd appearance in Chrome and Safari.
// 2. Correct the outline style in Safari.
.rcfm-fmtag[type=search]
  -webkit-appearance: textfield // 1
  outline-offset: -2px // 2

// Remove the inner padding in Chrome and Safari on macOS.
.rcfm-fmtag::-webkit-search-decoration
  -webkit-appearance: none

// 1. Correct the inability to style clickable types in iOS and Safari.
// 2. Change font properties to `inherit` in Safari.
.rcfm-fmtag::-webkit-file-upload-button
  -webkit-appearance: button // 1
  font: inherit // 2

// remove webkit number input buttons
.rcfm-fmtag::-webkit-inner-spin-button,
.rcfm-fmtag::-webkit-outer-spin-button
  display: none

// remove firefox number input buttons
.rcfm-fmtag[type=number]
  -moz-appearance: textfield

// remove firefox range input focus dotted outline
.rcfm-fmtag[type=range]::-moz-focus-outer
  border: none

// remove firefox invalid box shadow
.rcfm-fmtag:invalid
  box-shadow: none

// remove ie select icon
select.rcfm-fmtag::-ms-expand
  display: none

// remove ie clear button
.rcfm-fmtag::-ms-clear
  display: none

//remove search input cancel button
.rcfm-fmtag::-webkit-search-cancel-button
  -webkit-appearance: none

// remove datalist icon
.rcfm-fmtag::-webkit-calendar-picker-indicator
  display: none

// remove safari contact icon
.rcfm-fmtag::-webkit-contacts-auto-fill-button
  display: none !important

// remove safari password icon
.rcfm-fmtag::-webkit-credentials-auto-fill-button
  display: none !important

// remove safari caps icon
.rcfm-fmtag::-webkit-caps-lock-indicator
  display: none !important

.rcfm-fmtag:focus
  outline: none

.rcfm-fmtag
  -ms-touch-action: manipulation
  touch-action: manipulation

fieldset.rcfm-fmtag
  border: none

legend.rcfm-fmtag
  display: table
  max-width: 100%
  white-space: normal
  color: inherit

.rcfm-fmtag[disabled],
.rcfm-fmtag[readonly]
  cursor: not-allowed
  opacity: $form-tag-disabled-opacity
