.rcfm-fminput,
.rcfm-fmselect,
.rcfm-fmcheck,
.rcfm-fmradio,
.rcfm-fmtextarea
  display: flex
  flex-wrap: wrap
  font-size: $form-element-font-size
  color: $form-element-color

.rcfm-fminput > *,
.rcfm-fmselect > *,
.rcfm-fmcheck > *,
.rcfm-fmradio > *,
.rcfm-fmtextarea > *
  flex: 0 0 auto

.rcfm-fm-small.rcfm-fminput,
.rcfm-fm-small.rcfm-fmselect,
.rcfm-fm-small.rcfm-fmcheck,
.rcfm-fm-small.rcfm-fmradio,
.rcfm-fm-small.rcfm-fmtextarea
  font-size: $form-element-small-font-size

.rcfm-fm-large.rcfm-fminput,
.rcfm-fm-large.rcfm-fmselect,
.rcfm-fm-large.rcfm-fmcheck,
.rcfm-fm-large.rcfm-fmradio,
.rcfm-fm-large.rcfm-fmtextarea
  font-size: $form-element-large-font-size



.rcfm-fminput input,
.rcfm-fmtextarea textarea,
.rcfm-fmselect input,
.rcfm-fmselect select
  width: 100%
  border: $form-element-border-style $form-element-border-width $form-element-border-color
  border-radius: $form-element-border-radius
  box-shadow: $form-element-box-shadow

.rcfm-fminput input,
.rcfm-fmselect input,
.rcfm-fmselect select:not([multiple])
  height: $form-element-height
  line-height: normal // fix safari blinking cursor not in input center

.rcfm-fminput input,
.rcfm-fmselect select:not([multiple])
  padding: $form-element-padding-em
  background: $form-element-background

.rcfm-fmselect input
  padding: $form-element-with-icon-padding-em
  background: $form-element-single-select-background

.rcfm-fmtextarea textarea,
.rcfm-fmselect select[multiple]
  height: $form-element-multiple-rows-height
  min-height: $form-element-multiple-rows-height
  padding: $form-element-multiple-rows-padding-em
  line-height: $form-element-multiple-rows-line-height
  overflow: auto
  -webkit-overflow-scrolling: touch
  background: $form-element-background

.rcfm-fminput input:focus,
.rcfm-fmselect :focus ~ input,
.rcfm-fmtextarea textarea:focus,
.rcfm-fmselect select[multiple]:focus
  color: $form-element-focus-color
  border-color: $form-element-focus-border-color
  box-shadow: $form-element-focus-box-shadow

.rcfm-fminput input:focus,
.rcfm-fmtextarea textarea:focus,
.rcfm-fmselect select[multiple]:focus
  background: $form-element-focus-background

.rcfm-fmselect :focus ~ input
  background: $form-element-single-select-focus-background

.rcfm-fminput.rcfm-fm-invalid input:not(:focus),
.rcfm-fmselect.rcfm-fm-invalid :not(:focus) ~ input,
.rcfm-fmtextarea.rcfm-fm-invalid textarea:not(:focus),
.rcfm-fmselect.rcfm-fm-invalid select[multiple]:not(:focus)
  color: $form-element-invalid-color
  border-color: $form-element-invalid-border-color
  box-shadow: $form-element-invalid-box-shadow

.rcfm-fminput.rcfm-fm-invalid input:not(:focus),
.rcfm-fmtextarea.rcfm-fm-invalid textarea:not(:focus),
.rcfm-fmselect.rcfm-fm-invalid select[multiple]:not(:focus)
  background: $form-element-invalid-background

.rcfm-fmselect.rcfm-fm-invalid :not(:focus) ~ input
  background: $form-element-single-select-invalid-background

.rcfm-fm-small.rcfm-fminput input,
.rcfm-fm-small.rcfm-fmselect input,
.rcfm-fm-small.rcfm-fmselect select:not([multiple])
  height: $form-element-small-height

.rcfm-fm-small.rcfm-fmtextarea textarea,
.rcfm-fm-small.rcfm-fmselect select[multiple]
  height: $form-element-multiple-rows-small-height
  min-height: $form-element-multiple-rows-small-height
  line-height: $form-element-multiple-rows-small-line-height

.rcfm-fm-large.rcfm-fminput input,
.rcfm-fm-large.rcfm-fmselect input,
.rcfm-fm-large.rcfm-fmselect select:not([multiple])
  height: $form-element-large-height

.rcfm-fm-large.rcfm-fmtextarea textarea,
.rcfm-fm-large.rcfm-fmselect select[multiple]
  height: $form-element-multiple-rows-large-height
  min-height: $form-element-multiple-rows-large-height
  line-height: $form-element-multiple-rows-large-line-height

.rcfm-fmselect select:not([multiple])
  width: 100%
  opacity: 0

.rcfm-fmselect select:not([multiple]) + input
  margin-top: - $form-element-height

.rcfm-fm-small.rcfm-fmselect select:not([multiple]) + input
  margin-top: - $form-element-small-height

.rcfm-fm-large.rcfm-fmselect select:not([multiple]) + input
  margin-top: - $form-element-large-height

.rcfm-fmselect select[multiple] option
  display: flex
  align-items: center
  min-height: 0
  padding: 0



.rcfm-fmcheck label,
.rcfm-fmradio label
  flex: 0 1 auto
  padding: $form-element-checkfield-label-padding
  margin: $form-element-checkfield-label-margin
  line-height: $form-element-multiple-rows-line-height
  overflow: hidden
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none

.rcfm-fmcheck label input,
.rcfm-fmradio label input
  float: left
  margin: $form-element-checkfield-input-margin

.rcfm-fmcheck input,
.rcfm-fmradio input
  width: $form-element-checkfield-input-size-em
  height: $form-element-checkfield-input-size-em
  border: $form-element-border-style $form-element-border-width $form-element-border-color
  background: $form-element-background
  box-shadow: $form-element-box-shadow

.rcfm-fmcheck input
  border-radius: $form-element-border-radius
  transform: scale(0.9)

.rcfm-fmradio input
  border-radius: 100%

.rcfm-fmcheck input:checked,
.rcfm-fmradio input:checked
  background: $form-element-checkfield-checked-background

.rcfm-fmcheck input:focus,
.rcfm-fmradio input:focus
  border-color: $form-element-focus-border-color
  box-shadow: $form-element-focus-box-shadow

.rcfm-fmcheck input:not(:checked):focus,
.rcfm-fmradio input:not(:checked):focus
  background: $form-element-focus-background

.rcfm-fmcheck input:checked:focus,
.rcfm-fmradio input:checked:focus
  background: $form-element-checkfield-checked-focus-background

.rcfm-fmcheck.rcfm-fm-invalid input:not(:focus),
.rcfm-fmradio.rcfm-fm-invalid input:not(:focus)
  border-color: $form-element-invalid-border-color
  box-shadow: $form-element-invalid-box-shadow

.rcfm-fmcheck.rcfm-fm-invalid input:not(:checked),
.rcfm-fmradio.rcfm-fm-invalid input:not(:checked)
  background: $form-element-invalid-background

.rcfm-fmcheck.rcfm-fm-invalid input:checked,
.rcfm-fmradio.rcfm-fm-invalid input:checked
  background: $form-element-checkfield-checked-invalid-background

.rcfm-fm-small.rcfm-fmcheck label,
.rcfm-fm-small.rcfm-fmradio label
  line-height: $form-element-multiple-rows-small-line-height

.rcfm-fm-large.rcfm-fmcheck label,
.rcfm-fm-large.rcfm-fmradio label
  line-height: $form-element-multiple-rows-large-line-height

.rcfm-fm-small.rcfm-fmcheck label input,
.rcfm-fm-small.rcfm-fmradio label input
  margin: $form-element-checkfield-input-small-margin

.rcfm-fm-large.rcfm-fmcheck label input,
.rcfm-fm-large.rcfm-fmradio label input
  margin: $form-element-checkfield-input-large-margin
