.rcfm-btngroup-x,
.rcfm-btngroup-y
  display: inline-flex
  vertical-align: top
  max-width: 100%
  max-height: 100%
  box-shadow: $button-group-box-shadow

.rcfm-btngroup-x.rcfm-btngroup-fluid,
.rcfm-btngroup-y.rcfm-btngroup-fluid
  display: flex
  width: 100%



.rcfm-btngroup-x [class^=rcfm-btn-]:not(:first-child)
  border-left: none
  border-top-left-radius: 0
  border-bottom-left-radius: 0

.rcfm-btngroup-x [class^=rcfm-btn-]:not(:last-child)
  border-right: none
  border-top-right-radius: 0
  border-bottom-right-radius: 0

.rcfm-btngroup-x [class^=rcfm-btn-],
.rcfm-btngroup-y [class^=rcfm-btn-]
  box-shadow: none

.rcfm-btngroup-x.rcfm-btngroup-fluid [class^=rcfm-btn-]
  flex: 1 1 auto



.rcfm-btngroup-y
  flex-direction: column

.rcfm-btngroup-y [class^=rcfm-btn-]:not(:first-child)
  border-top: none
  border-top-right-radius: 0
  border-top-left-radius: 0

.rcfm-btngroup-y [class^=rcfm-btn-]:not(:last-child)
  border-bottom: none
  border-bottom-right-radius: 0
  border-bottom-left-radius: 0

.rcfm-btngroup-y [class^=rcfm-btn-]:not([class^=rcfm-btn-ico-])
  justify-content: flex-start

.rcfm-btngroup-y [class^=rcfm-btn-].rcfm-btn-ico-right
  justify-content: space-between

+browser-edge
  .rcfm-btngroup-y input[class^=rcfm-btn-]:not([class^=rcfm-btn-ico-])
    text-align: left
