.rcfm-tabs
  display: block

.rcfm-tabs-header
  display: flex
  justify-content: flex-start
  align-items: center
  padding: $tabs-header-padding
  font-weight: $tabs-header-font-weight
  overflow: hidden

.rcfm-tabs-title
  display: block
  flex: 1 1 100%
  margin: $tabs-title-margin
  overflow: hidden
  white-space: nowrap
  text-overflow: ellipsis
  font-size: $tabs-title-font-size
  color: $tabs-title-color
  line-height: $tabs-title-line-height

.rcfm-tabs-label
  display: block
  flex: 0 0 auto
  margin: $tabs-label-margin
  padding: $tabs-label-padding
  overflow: hidden
  white-space: nowrap
  text-overflow: ellipsis
  font-size: $tabs-label-font-size
  line-height: 1
  color: $tabs-label-color
  border-bottom: $tabs-label-border-style $tabs-label-border-width $tabs-label-border-color
  cursor: pointer
  -ms-touch-action: manipulation
  touch-action: manipulation
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none

.rcfm-tabs-label:first-of-type
  margin-left: 0

.rcfm-tabs-label:hover
  color: $tabs-label-hover-color
  border-color: $tabs-label-hover-border-color

.rcfm-tabs-label.rcfm-tabs-label-active
  color: $tabs-label-active-color
  border-color: $tabs-label-active-border-color

.rcfm-tabs-pane
  display: none
  padding: $tabs-pane-padding
  overflow: auto
  -webkit-overflow-scrolling: touch

.rcfm-tabs-pane-active.rcfm-tabs-pane
  display: block
