[class^=rcfm-btn-].rcfm-btn-ico-middle
  width: 0
  padding: 0

[class^=rcfm-btn-].rcfm-btn-ico-middle [class^=rcfm-ico-],
[class^=rcfm-btn-].rcfm-btn-ico-left [class^=rcfm-ico-],
[class^=rcfm-btn-].rcfm-btn-ico-right [class^=rcfm-ico-]
  font-size: $button-icon-size
  pointer-events: none



[class^=rcfm-btn-].rcfm-btn-ico-left [class^=rcfm-ico-]
  order: -1
  margin: $button-icon-left-margin



[class^=rcfm-btn-].rcfm-btn-ico-right [class^=rcfm-ico-]
  order: 1
  margin: $button-icon-right-margin



[class^=rcfm-btn-].rcfm-btn-ico-top
  flex-direction: column

[class^=rcfm-btn-].rcfm-btn-ico-top
  height: $button-icon-top-height

[class^=rcfm-btn-].rcfm-btn-ico-top [class^=rcfm-ico-]
  order: -1
  margin: $button-icon-top-margin
  font-size: $button-icon-top-size
  pointer-events: none



.rcfm-btn-large.rcfm-btn-ico-top
  height: $button-icon-top-large-height

.rcfm-btn-small.rcfm-btn-ico-top
  height: $button-icon-top-small-height
