// sass-lint:disable max-line-length

=button-solid($name: null, $color: null, $hover-color: null, $active-color: null, $background: null, $hover-background: null, $active-background: null, $border-color: null, $hover-border-color: null, $active-border-color: null, $box-shadow: null, $hover-box-shadow: null, $active-box-shadow: null)

  .rcfm-btn-#{$name}
    color: $color
    background: $background
    border-color: $border-color
    box-shadow: $box-shadow

  .rcfm-btn-#{$name}:not([disabled]):hover
    color: $hover-color
    background: $hover-background
    border-color: $hover-border-color
    box-shadow: $hover-box-shadow

  .rcfm-btn-#{$name}:not([disabled]):active,
  .rcfm-btn-#{$name}.rcfm-btn-active:not([disabled])
    color: $active-color
    background: $active-background
    border-color: $active-border-color
    box-shadow: $active-box-shadow



=button-hollow($name: null, $color: null, $hover-color: null, $active-color: null, $hover-background: null, $active-background: null, $border-color: null, $hover-border-color: null, $active-border-color: null, $box-shadow: null, $hover-box-shadow: null, $active-box-shadow: null)

  .rcfm-btn-hollow-#{$name}
    color: $color
    border-color: $border-color
    box-shadow: $box-shadow

  .rcfm-btn-hollow-#{$name}:not([disabled]):hover
    color: $hover-color
    background: $hover-background
    border-color: $hover-border-color
    box-shadow: $hover-box-shadow

  .rcfm-btn-hollow-#{$name}:not([disabled]):active,
  .rcfm-btn-hollow-#{$name}.rcfm-btn-active:not([disabled])
    color: $active-color
    background: $active-background
    border-color: $active-border-color
    box-shadow: $active-box-shadow



=button-text($name: null, $color: null, $hover-color: null, $active-color: null, $hover-background: null, $active-background: null)

  .rcfm-btn-text-#{$name}
    color: $color

  .rcfm-btn-text-#{$name}:not([disabled]):hover
    color: $hover-color
    background: $hover-background

  .rcfm-btn-text-#{$name}:not([disabled]):active,
  .rcfm-btn-text-#{$name}.rcfm-btn-active:not([disabled])
    color: $active-color
    background: $active-background
