.rcfm-alert,
.rcfm-alert-info,
.rcfm-alert-success,
.rcfm-alert-error,
.rcfm-alert-warning
  z-index: $z-index-higher
  display: block
  padding: $alert-padding
  overflow: hidden
  border-radius: $alert-border-radius
  box-shadow: $alert-box-shadow

.rcfm-alert-title
  margin: $alert-title-margin
  font-size: $alert-title-font-size
  line-height: $alert-title-line-height
  font-weight: $alert-title-font-weight

.rcfm-alert-text
  margin: $alert-text-margin
  font-size: $alert-text-font-size
  line-height: $alert-text-line-height

.rcfm-alert-info
  color: $alert-info-color
  background: $alert-info-background

.rcfm-alert-success
  color: $alert-success-color
  background: $alert-success-background

.rcfm-alert-error
  color: $alert-error-color
  background: $alert-error-background

.rcfm-alert-warning
  color: $alert-warning-color
  background: $alert-warning-background
