.rcfm-fmlabel
  display: block
  font-size: $form-label-font-size
  line-height: $form-label-line-height
  color: $form-label-color

.rcfm-fmlabel-required.rcfm-fmlabel::after
  content: $form-label-required-content
  color: $form-label-required-content-color

.rcfm-fmlabel-small.rcfm-fmlabel
  font-size: $form-label-small-font-size
  line-height: $form-label-small-line-height

.rcfm-fmlabel-large.rcfm-fmlabel
  font-size: $form-label-large-font-size
  line-height: $form-label-large-line-height
