.rcfm-form-validation-editor-item
  display: flex
  justify-content: space-between
  align-items: center
  margin: $form-validation-editor-item-margin

.rcfm-form-validation-editor-item:first-child
  margin-top: 0

.rcfm-form-validation-editor-item:last-child
  margin-bottom: 0

.rcfm-form-validation-editor-item > [class^=rcfm-]
  flex: 1 1 50%
  margin: $form-validation-editor-item-elements-margin

.rcfm-form-validation-editor-item > .rcfm-btngroup-x
  flex: 0 0 auto

.rcfm-form-validation-editor-item > [class^=rcfm-]:first-child
  margin-left: 0

.rcfm-form-validation-editor-item > [class^=rcfm-]:last-child
  margin-right: 0

.rcfm-form-validation-editor-item > [class^=rcfm-btngroup-]
  flex: 0 0 auto
